import 'react-app-polyfill/ie11';
import React, { useState, useEffect } from 'react';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import lottie from 'lottie-web';

import './App.css';


const Transition = React.forwardRef((props, ref) => (
  <Slide direction="up" ref={ref} {...props} />
));

const App = () => {
  const [open, setOpen] = useState({opened: false, modal: null});
  const [openQuestion, setOpenQuestion] = useState(false);

  useEffect(() => {
    const anim = lottie.loadAnimation({
      container: document.getElementById('lottie'),
      renderer: 'svg',
      loop: true,
      autoplay: true,
      path: 'cross.json'
    });
    anim.addEventListener('DOMLoaded', (e) => {
      const human = document.getElementsByClassName('human-cross');
      for (let i = 0; i < human.length; i++) {
        human[i].onclick = () => {
          for (let j = 0; j < human.length; j++) {
            human[j].classList.toggle('red');
          }
        };
      }
      const sportcar = document.getElementsByClassName('sportcar');
      for (let i = 0; i < sportcar.length; i++) {
        sportcar[i].onclick = () => {
          for (let j = 0; j < sportcar.length; j++) {
            sportcar[j].classList.toggle('red');
          }
        };
      }
      const question = document.getElementsByClassName('question');
      for (let i = 0; i < question.length; i++) {
        question[i].onclick = openQuestionModal;
      }
    });
  }, []);
  
  function openProfileModal() {
    setOpen({opened: true, modal: 'profile'});
  }

  function openCloseModal() {
    setOpen({opened: true, modal: 'close'});
  }

  function closeModal() {
    setOpen({opened: false, modal: null});
  }

  function openQuestionModal() {
    setOpenQuestion(true);
  }

  function closeQuestion() {
    setOpenQuestion(false);
  }
  
  return (
    <div className="app">
      <div className="container">
        <Grid container justify="center" className="lottie-wrapper">
          <Grid item>
            <div id="lottie">
            </div>
          </Grid>
        </Grid>
        <Grid container direction="column" justify="space-between">
          <Grid container item direction="row" justify="space-between">
            <Grid item >
              <div className="title">
                <img alt="Logo" src="logo.svg" width="300"/>
              </div>
            </Grid>
            <Grid item>
              <div className="profile">
                <Grid container alignItems="center">
                  <Grid item className="pupil">
                    <div className="name">
                      Святозар Игнатов
                    </div>
                    <div className="number">
                      1Б
                    </div>
                  </Grid>
                  <Grid item>  
                    <div className="title">
                      <IconButton size='small' onClick={openProfileModal}>
                        <img alt="Logo" src="profile.svg" width="60"/>
                      </IconButton>
                    </div>
                  </Grid>
                </Grid>
              </div>
            </Grid>
          </Grid>
          <Grid container item alignItems="flex-end" justify="space-between">
            <Grid item>
              <div className="back">
                <IconButton onClick={openCloseModal}>
                  <img alt="Logo" src="undo.svg" width="60"/>
                </IconButton>
              </div>
            </Grid>
            <Grid item>
              <div className="school">
                <div>Государственное бюджетное</div>
                <div>общеобразовательное учреждение</div>
                <div>средняя шока №123</div>
                <div>с углубленным изучением предметов</div>
                <div>естественнонаучного цикла</div>
                <div>Центрального района Санкт-Петербурга</div>
              </div>
            </Grid>
          </Grid>  
        </Grid>
        <Dialog
          open={open.opened}
          TransitionComponent={Transition}
          keepMounted
          onClose={closeModal}
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"
        >
          <DialogTitle id="alert-dialog-slide-title">{open.modal === 'profile' ? "Профиль ученика" : 'Вернуться на карту'}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-slide-description">
              {open.modal === 'profile' ?
                'Профиль ученика 1Б Святозара Игнатова' :
                'Экзамен еще не закончен. Вы уверены что хотите завершить тестирование?'}
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={closeModal} color="primary">
              {open.modal === 'profile' ? 'Выйти из профиля' : 'Завершить тестирование'}
            </Button>
            <Button onClick={closeModal} color="primary">
              Закрыть
            </Button>
          </DialogActions>
        </Dialog>
        <Dialog
          open={openQuestion}
          TransitionComponent={Transition}
          keepMounted
          onClose={closeQuestion}
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"
        >
          <DialogTitle id="alert-dialog-slide-title">{'Вопрос'}</DialogTitle>
          <DialogContent>
            <img alt="Sign" src="sign.svg" width="200"/>
          </DialogContent>
          <DialogActions>
            <Button onClick={closeQuestion} color="primary">
              Закрыть
            </Button>
          </DialogActions>
        </Dialog>
      </div>  
    </div>  
  );
}

export default App;
